import { preorderTraverse } from "@visactor/vseed";
import { pipe } from "remeda";
class MeasureNodeBuilder {
    measureNode;
    constructor(measureNode){
        this.measureNode = measureNode;
    }
    setAlias(alias) {
        this.measureNode.alias = alias;
        return this;
    }
    setEncoding(encoding) {
        this.measureNode.encoding = encoding;
        return this;
    }
    setAggregate(aggregate) {
        this.measureNode.aggregate = aggregate;
        return this;
    }
    build() {
        return {
            ...this.measureNode
        };
    }
    static from(measureNode) {
        return new MeasureNodeBuilder(measureNode);
    }
}
class MeasuresBuilder {
    measures = [];
    context;
    addMeasure(fieldOrMeasure, callback) {
        const defaultMeasure = {};
        if ('string' == typeof fieldOrMeasure) {
            defaultMeasure.alias = fieldOrMeasure;
            defaultMeasure.field = fieldOrMeasure;
            defaultMeasure.encoding = 'yAxis';
            defaultMeasure.aggregate = {
                func: 'sum'
            };
        } else {
            defaultMeasure.alias = fieldOrMeasure.alias;
            defaultMeasure.field = fieldOrMeasure.field;
            defaultMeasure.encoding = fieldOrMeasure.encoding;
            defaultMeasure.aggregate = fieldOrMeasure.aggregate;
        }
        const measureNode = MeasureNodeBuilder.from({
            ...defaultMeasure
        });
        this.measures.push(measureNode);
        if (!callback) return measureNode;
        callback(measureNode);
        return this;
    }
    build() {
        return this.measures.map((measureNode)=>measureNode.build());
    }
    constructor(context){
        this.context = context;
    }
    static from(measures, context) {
        const builder = new MeasuresBuilder(context);
        preorderTraverse(measures, (measure)=>{
            if (this.isMeasureNode(measure)) builder.addMeasure(measure);
            else this.isMeasureGroup(measure);
            return false;
        });
        return builder;
    }
    static isMeasureNode(node) {
        return 'field' in node;
    }
    static isMeasureGroup(node) {
        return 'children' in node;
    }
}
class DimensionNodeBuilder {
    dimensionNode;
    constructor(dimensionNode){
        this.dimensionNode = dimensionNode;
    }
    setAlias(alias) {
        this.dimensionNode.alias = alias;
        return this;
    }
    build() {
        return {
            ...this.dimensionNode
        };
    }
    static from(dimensionNode) {
        return new DimensionNodeBuilder(dimensionNode);
    }
}
class DimensionsBuilder {
    dimensions = [];
    context;
    addDimension(fieldOrDimension, callback) {
        const defaultDimension = {};
        if ('string' == typeof fieldOrDimension) {
            defaultDimension.alias = fieldOrDimension;
            defaultDimension.field = fieldOrDimension;
        } else {
            defaultDimension.alias = fieldOrDimension.alias;
            defaultDimension.field = fieldOrDimension.field || fieldOrDimension.alias;
        }
        const dimensionNode = DimensionNodeBuilder.from({
            ...defaultDimension
        });
        this.dimensions.push(dimensionNode);
        if (!callback) return dimensionNode;
        callback(dimensionNode);
        return this;
    }
    build() {
        return this.dimensions.map((dimensionNode)=>dimensionNode.build());
    }
    constructor(context){
        this.context = context;
    }
    static from(dimensions, context) {
        const builder = new DimensionsBuilder(context);
        preorderTraverse(dimensions, (dimension)=>{
            if (this.isDimensionNode(dimension)) builder.addDimension(dimension);
            else this.isDimensionGroup(dimension);
            return false;
        });
        return builder;
    }
    static isDimensionNode(node) {
        return 'alias' in node && !('children' in node);
    }
    static isDimensionGroup(node) {
        return 'children' in node;
    }
}
class BuilderContext {
    vbiBuilder;
    constructor(builder){
        this.vbiBuilder = builder;
    }
    getVBIDSL() {
        return this.vbiBuilder.vbiDSL;
    }
    static from = (builder)=>new BuilderContext(builder);
}
const buildVQuery = (vbiDSL, builder)=>{
    const wrapper = (processor)=>(queryDSL)=>processor(queryDSL, {
                vbiDSL,
                builder
            });
    return pipe({}, wrapper(buildSelect), wrapper(buildGroupBy), wrapper(buildLimit));
};
const buildSelect = (queryDSL, context)=>{
    const { vbiDSL } = context;
    const measures = vbiDSL.measures;
    const dimensions = vbiDSL.dimensions;
    const result = {
        ...queryDSL
    };
    const measureNodes = measures.filter((measure)=>MeasuresBuilder.isMeasureNode(measure));
    const measureSelects = measureNodes.map((measure)=>({
            field: measure.field,
            alias: measure.alias,
            func: measure.aggregate.func
        }));
    const dimensionNodes = dimensions.filter((dimension)=>DimensionsBuilder.isDimensionNode(dimension));
    const dimensionSelects = dimensionNodes.map((dimension)=>({
            field: dimension.field,
            alias: dimension.alias
        }));
    result.select = measureSelects.concat(dimensionSelects);
    return result;
};
const buildGroupBy = (queryDSL, context)=>{
    const result = {
        ...queryDSL
    };
    const { vbiDSL } = context;
    const dimensions = vbiDSL.dimensions;
    const dimensionNodes = dimensions.filter((dimension)=>DimensionsBuilder.isDimensionNode(dimension));
    result.groupBy = dimensionNodes.map((dimension)=>dimension.field);
    return result;
};
const buildLimit = (queryDSL)=>{
    const result = {
        ...queryDSL
    };
    result.limit = 1000;
    return result;
};
class VBIBuilder {
    vbi;
    _vbiDSL;
    measures;
    dimensions;
    constructor(vbi){
        this.vbi = vbi;
        this._vbiDSL = vbi;
        const context = BuilderContext.from(this);
        this.measures = MeasuresBuilder.from(vbi.measures || [], context);
        this.dimensions = DimensionsBuilder.from(vbi.dimensions || [], context);
    }
    buildVSeed = async ()=>{
        const connectorId = this._vbiDSL.connectorId;
        const connector = await VBIBuilder.getConnector(this._vbiDSL.connectorId);
        const queryDSL = this.buildVQuery();
        const schema = await connector.discoverSchema();
        const queryResult = await connector.query({
            queryDSL,
            schema,
            connectorId
        });
        console.log('debug queryDSL', queryDSL);
        return {
            chartType: 'table',
            dataset: queryResult.dataset
        };
    };
    buildVQuery = ()=>buildVQuery(this.build(), this);
    build() {
        const result = {
            ...this._vbiDSL,
            measures: this.measures.build(),
            dimensions: this.dimensions.build()
        };
        return result;
    }
    get vbiDSL() {
        return this._vbiDSL;
    }
    static connectorMap = new Map();
    static registerConnector = (id, connector)=>{
        this.connectorMap.set(id, connector);
    };
    static getConnector = async (id)=>{
        const connector = this.connectorMap.get(id);
        if (!connector) throw new Error(`connector ${id} not registered`);
        if ('function' == typeof connector) return connector();
        return connector;
    };
    static from = (vbi)=>new VBIBuilder(vbi);
    static generateEmptyDSL = (connectorId)=>({
            connectorId: connectorId,
            chartType: 'table',
            measures: [],
            dimensions: [],
            theme: 'light',
            locale: 'zh-CN',
            version: 0
        });
}
export { DimensionsBuilder, MeasuresBuilder, VBIBuilder as VBI, VBIBuilder };
